import Link from "next/link"
import { ArrowRight, FileText, Video, Users, Check } from "lucide-react"
import { Button } from "@/components/ui/button"
import { services } from "@/lib/data"

const iconMap: Record<string, React.ComponentType<{ className?: string }>> = {
  "resume-review": FileText,
  "resume-rewrite": FileText,
  "mock-interview": Video,
  "career-coaching": Users,
}

export function ServicesSection() {
  return (
    <section className="bg-off-white py-24 sm:py-32">
      <div className="mx-auto max-w-7xl px-6 lg:px-8">
        {/* Section Header */}
        <div className="mx-auto max-w-2xl text-center">
          <p className="text-sm font-semibold uppercase tracking-widest text-neon">Personalized Support</p>
          <h2 className="mt-2 text-3xl font-bold tracking-tight text-midnight sm:text-4xl">
            1-on-1 Services
          </h2>
          <p className="mt-4 text-lg text-slate">
            Get personalized feedback and coaching from experienced tech sales professionals.
          </p>
        </div>

        {/* Services Grid */}
        <div className="mt-16 grid grid-cols-1 gap-8 lg:grid-cols-2">
          {services.slice(0, 2).map((service) => {
            const Icon = iconMap[service.id] || FileText
            return (
              <div
                key={service.id}
                className="relative overflow-hidden rounded-2xl bg-white p-8 shadow-sm ring-1 ring-black/5"
              >
                <div className="flex items-start gap-4">
                  <div className="flex h-12 w-12 shrink-0 items-center justify-center rounded-xl bg-neon/10 text-neon">
                    <Icon className="h-6 w-6" />
                  </div>
                  <div className="flex-grow">
                    <div className="flex items-start justify-between gap-4">
                      <h3 className="text-xl font-bold text-midnight">{service.title}</h3>
                      <span className="text-2xl font-bold text-midnight">${service.price}</span>
                    </div>
                    <p className="mt-2 text-sm text-slate">{service.description}</p>
                    <p className="mt-3 text-xs font-medium text-electric">
                      Turnaround: {service.turnaround}
                    </p>
                  </div>
                </div>

                <ul className="mt-6 grid grid-cols-1 gap-2 sm:grid-cols-2">
                  {service.includes.slice(0, 4).map((item) => (
                    <li key={item} className="flex items-center gap-2 text-sm text-slate">
                      <Check className="h-4 w-4 shrink-0 text-neon" />
                      {item}
                    </li>
                  ))}
                </ul>

                <div className="mt-6">
                  <Button variant="outline" className="w-full" asChild>
                    <Link href="/services">Learn More</Link>
                  </Button>
                </div>
              </div>
            )
          })}
        </div>

        {/* View All Link */}
        <div className="mt-12 text-center">
          <Button variant="ghost" asChild>
            <Link href="/services" className="gap-2 text-electric hover:text-electric">
              View All Services
              <ArrowRight className="h-4 w-4" />
            </Link>
          </Button>
        </div>
      </div>
    </section>
  )
}

